import LanguageDropdown from "@/components/LanguageDropdown";
import Link from "next/link";
import { FC, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaHamburger } from "react-icons/fa";
import { FaBarsStaggered, FaPix } from "react-icons/fa6";
import { PiX } from "react-icons/pi";

type Props = {
  active: string;
  setUrl: (tab: string) => void;
};

const Header: FC<Props> = ({ active, setUrl }) => {
  const { t } = useTranslation();
  const [menuOpen, setMenuOpen] = useState(false);

  const handleToggle = () => {
    setMenuOpen(!menuOpen);
  };

  const navLinks = (
    <>
      <span
        onClick={() => {
          setUrl("home");
          setMenuOpen(false);
        }}
        className={`${active === "home" || active === "" ? "text-white" : "text-gray-500"
          } cursor-pointer uppercase`}
      >
        {t("landing.sections.menu.home")}
      </span>
      <span
        onClick={() => {
          setUrl("products");
          setMenuOpen(false);
        }}
        className={`${active === "products" ? "text-white" : "text-gray-500"
          } cursor-pointer uppercase`}
      >
        {t("landing.sections.menu.products")}
      </span>
      <span
        onClick={() => {
          setUrl("aboutus");
          setMenuOpen(false);
        }}
        className={`${active === "aboutus" ? "text-white" : "text-gray-500"
          } cursor-pointer uppercase`}
      >
        {t("landing.sections.menu.about_us")}
      </span>
    </>
  );

  return (
    <div
      className={`w-full z-[99] flex justify-between items-center !bg-black/10 ${active === "home" || active === "" ? "absolute z-10" : ""
        } !p-8`}
    >
      <div>
        <img src="/img/logo-inverse.svg" className="w-36 hidden md:block" alt="Logo" />
        <div className="block md:hidden">
          <LanguageDropdown />
        </div>
      </div>


      <div className="hidden md:flex gap-4 items-center">{navLinks}</div>

      <div className="hidden  gap-2 items-center">
        <Link className="uppercase" href="/login">
          {t("landing.sections.menu.sign_up")}
        </Link>
        <LanguageDropdown />
      </div>

      <div className="md:hidden hidden items-center">
        <button onClick={handleToggle}>
          {menuOpen ? (
            <PiX className="h-6 w-6 text-white" />
          ) : (
            <FaBarsStaggered className="h-6 w-6 text-white" />
          )}
        </button>
      </div>

      {menuOpen && (
        <div className="absolute top-20 left-0 w-full bg-black/90 flex flex-col items-center gap-4 py-6 md:hidden z-20">
          {navLinks}
          <Link
            className="uppercase text-white"
            href="/login"
            onClick={() => setMenuOpen(false)}
          >
            {t("landing.sections.menu.sign_up")}
          </Link>
          <LanguageDropdown />
        </div>
      )}
    </div>
  );
};

export default Header;
