import AboutUs from "@/components/views/landing/AboutUs";
import Header from "@/components/views/landing/Header";
import Home from "@/components/views/landing/Home";
import Products from "@/components/views/landing/Products";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";

const Page = () => {
  const router = useRouter();
  const [active, setActive] = useState("");
  const setUrl = (tab: string) => {
    setActive(tab);
  };
  console.log(active);
  return (
    <div className="h-full bg-transparent">
      <Header active={active} setUrl={(tab) => setUrl(tab)} />
      {active == "home" || active == "" ? <Home /> : null}
      {active == "products" && <Products />}
      {active == "aboutus" && <AboutUs />}
    </div>
  );
};

export default Page;
